html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem; }
  body.fijar-body {
    overflow: hidden; }

p {
  color: #000;
  font-size: 2rem; }

.contenedor {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  margin: 0; }

h1 {
  font-size: 4rem;
  font-weight: 900; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

.bg-verde {
  background-color: #4CB8B3; }

.bg-amarillo {
  background-color: #FEF46D; }

.header {
  background-color: #4CB8B3; }
  .header .contenido-header {
    padding: 1.66667rem; }
    @media (min-width: 480px) {
      .header .contenido-header {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
  .header h1 {
    cursor: pointer;
    color: #ffff;
    margin-bottom: 1rem;
    text-align: center; }
    @media (min-width: 768px) {
      .header h1 {
        margin-bottom: 0; } }
  .header.fijo {
    position: fixed;
    width: 100%;
    box-shadow: 0 .3rem 4rem #444; }

.navegacion-principal {
  display: flex;
  gap: 2rem;
  justify-content: space-around;
  column-gap: 2rem; }
  .navegacion-principal a {
    color: #ffff; }
    .navegacion-principal a:hover {
      color: #FEF46D; }

.video {
  overflow: hidden;
  position: relative;
  max-height: 45rem; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+67,ffffff+100&0.8+0,0.5+100 */
    background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#80ffffff',GradientType=1 );
    /* IE6-9 fallback on horizontal gradient */ }
  .video video {
    display: block;
    width: 100%; }

.contenido-video {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 1; }
  .contenido-video h2 {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    color: #ffff;
    font-size: 2rem;
    align-items: flex-start; }
    .contenido-video h2 span {
      background-color: #F53766;
      font-size: 1rem;
      padding: 1rem 2rem; }
    @media (min-width: 768px) {
      .contenido-video h2 {
        font-size: 5rem; }
        .contenido-video h2 span {
          background-color: #F53766;
          font-size: 2rem;
          padding: 1rem 2rem; } }
    @media (min-width: 1200px) {
      .contenido-video h2 {
        font-size: 7rem; }
        .contenido-video h2 span {
          background-color: #F53766;
          font-size: 2rem;
          padding: 1rem 2rem; } }

.informacion {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  margin-top: 2rem; }
  .informacion h3, .informacion .fecha-informacion {
    text-align: center; }
  .informacion .fecha-informacion {
    color: #F53766;
    font-weight: bold; }
  .informacion .imagen-informacion {
    cursor: pointer; }

@media (min-width: 768px) {
  h3, .fecha-informacion {
    text-align: justify; }
  .contenido-informacion {
    padding: 2rem;
    justify-content: center;
    column-gap: 2rem; }
  .fecha-informacion {
    color: #F53766;
    font-weight: bold; } }

@media (min-width: 1200px) {
  .contenido-informacion {
    padding: 3rem;
    display: flex;
    align-items: center; } }

