.lineup {
  background-color: #F53766;
  padding: 5rem 0; }
  .lineup h3, .lineup p {
    text-align: center;
    color: #ffff; }
  .lineup .dia {
    text-transform: uppercase;
    margin: 5rem 0; }

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem; } }

.escenarios-contenedor .escenario {
  padding-top: 2.5rem; }

.escenarios-contenedor .nombre-escenario {
  color: #000;
  font-size: 2rem;
  font-weight: 300; }
  .escenarios-contenedor .nombre-escenario:after {
    content: '';
    width: 20rem;
    height: 10rem;
    display: block;
    margin: 2rem auto 0 auto;
    background-size: 15rem;
    background-repeat: no-repeat;
    background-position: center center; }

.rock .nombre-escenario:after {
  background-image: url(/FestivalMusica_inicio/src/img/icono_rock.png); }

.edm .nombre-escenario:after {
  background-image: url(/FestivalMusica_inicio/src/img/icono_edm.png); }

.calendario {
  list-style: none;
  padding: 0; }
  .calendario li {
    padding: 2.5rem;
    font-weight: 900; }
    .calendario li span {
      font-weight: 400; }

.bg-verde .calendario li:nth-child(odd) {
  background-color: #3b9691; }

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: #eada02; }

.galeria {
  background-color: #ffff;
  padding: 1rem;
  margin: 0 auto;
  text-align: center; }
  .galeria h3 {
    margin-bottom: 3rem;
    text-align: center;
    color: #752f97; }
  .galeria .galeria-imagenes {
    padding: 0;
    display: grid;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1.66667rem; }
    @media (min-width: 768px) {
      .galeria .galeria-imagenes {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        column-gap: 1.66667rem; } }
    @media (min-width: 1200px) {
      .galeria .galeria-imagenes {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        column-gap: 1.66667rem; } }
  .galeria li {
    list-style: none; }
  .galeria img {
    cursor: pointer; }

@media (min-width: 768px) {
  .galeria {
    padding: 3rem; } }

@media (min-width: 1200px) {
  .galeria {
    padding: 10rem; } }

.overlay {
  position: fixed;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ea5b5d+0,f0fc11+100&0.32+0,0.32+100 */
  background: -moz-linear-gradient(-45deg, rgba(234, 91, 93, 0.32) 0%, rgba(240, 252, 17, 0.32) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(234, 91, 93, 0.32) 0%, rgba(240, 252, 17, 0.32) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(234, 91, 93, 0.32) 0%, rgba(240, 252, 17, 0.32) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#52ea5b5d', endColorstr='#52f0fc11',GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center; }
  .overlay img {
    width: 90%; }

@media (min-width: 768px) {
  .overlay img {
    width: 95%; } }

@media (min-width: 1200px) {
  .overlay img {
    width: 55%; } }

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  background: linear-gradient(to top, #4CB8B3, #FEF46D);
  border: 2px solid black;
  border-radius: 50%;
  display: grid;
  place-items: center;
  font-weight: 900;
  font-size: 3rem; }
  .btn-cerrar:hover {
    background: linear-gradient(to top, #F53766, #752f97);
    cursor: pointer; }

.boletos {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  column-gap: 5rem;
  padding: 5rem 0; }
  .boletos h3 {
    text-align: center;
    color: #ffff; }
  .boletos .plan p {
    color: #ffff;
    font-size: 3rem;
    text-align: center;
    margin-bottom: 2rem; }
  .boletos .contenedor-boletos {
    margin-top: 1rem; }
  @media (min-width: 768px) {
    .boletos .contenedor-boletos {
      margin-top: 1rem;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 5rem; } }
  @media (min-width: 1200px) {
    .boletos .contenedor-boletos {
      margin-top: 1rem;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 5rem; } }

.pase {
  width: 100%;
  border-spacing: 0; }
  .pase thead {
    background-color: #F53766;
    color: #ffff; }
    .pase thead th {
      padding: 1rem;
      font-weight: 300;
      font-size: 2rem;
      text-transform: uppercase; }
  .pase tbody {
    background-color: #ffff; }
    .pase tbody td {
      padding: 1.66667rem;
      border-bottom: 1px solid #F53766; }
    .pase tbody tr:last-of-type td {
      border-bottom: none; }
  .pase tfoot {
    background-color: #752f97;
    color: #ffff;
    text-align: center;
    font-size: 2rem; }
    .pase tfoot td {
      padding: 1.66667rem; }
    .pase tfoot:hover td {
      cursor: pointer; }

.footer {
  background-color: #F53766;
  text-align: center;
  padding: 0.83333rem; }
  .footer p {
    margin: 0 auto;
    color: #ffff;
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 900; }

@media (min-width: 1200px) {
  .footer p {
    font-size: 1.5rem; } }
